.result{
    height:100vh;
	display:grid;
	place-items:center;
}
.result_box{
	padding:10%;
	text-align:center;
	background-color:green;
	border-radius:10px;
	box-shadow:0 5px 6px rgba(0,0,0,0.12),0 2px 3px rgba(0,0,0,0.24);
	padding-top:50px;
	padding-bottom:50px;
}
.result_box > button{
	padding:8px 16px;
	border-radius:4px;
	color:black;
	background-color:white;
	border:1px solid black;
}
.result_box > button:hover{
	background-color:lightgrey;
}