@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.inputBox{
	display:grid;
	place-items:center;
	height:100vh;
	background-color:#f8f8f8;
}
.inputBox_field{
	padding:10%;
	text-align:center;
	background-color:white;
	border-radius:10px;
	box-shadow:0 5px 6px rgba(0,0,0,0.12),0 2px 3px rgba(0,0,0,0.24);
	padding-top:0;
	padding-bottom:50px;
	margin:20px;
}
.inputBox_field > h1{
	font-family:'Montserrat', sans-serif;
}
.inputBox_field > img{
	width:15%;
	padding-bottom:40px;
	padding-top:20px;
}
.inputBox_field > p{
	font-size:20px;
}

.result{
    height:100vh;
	display:grid;
	place-items:center;
}
.result_box{
	padding:10%;
	text-align:center;
	background-color:green;
	border-radius:10px;
	box-shadow:0 5px 6px rgba(0,0,0,0.12),0 2px 3px rgba(0,0,0,0.24);
	padding-top:50px;
	padding-bottom:50px;
}
.result_box > button{
	padding:8px 16px;
	border-radius:4px;
	color:black;
	background-color:white;
	border:1px solid black;
}
.result_box > button:hover{
	background-color:lightgrey;
}
